/* === CNMC Sky override patch (scoped) === */
body.cnmc-sky{
  font-family:"Noto Sans TC","Microsoft JhengHei",system-ui,-apple-system,sans-serif !important;
  line-height:1.8 !important;
  color:#0f172a !important;

  /* 藍天之子概念：天空淡化背景 + 亮白內容 */
  background:
    linear-gradient(180deg, rgba(240,249,255,.92) 0%, rgba(224,242,254,.92) 45%, rgba(255,255,255,.98) 100%),
    url("../dist/bsky.33c8ef83.jpg") !important;
  background-size:cover !important;
  background-attachment:fixed !important;
}

/* 內容收斂：不動文字，先讓閱讀像正式介紹頁 */
body.cnmc-sky main,
body.cnmc-sky article,
body.cnmc-sky .content,
body.cnmc-sky .container{
  max-width: 980px !important;
  margin: 0 auto !important;
  padding: 24px 16px 72px !important;
}

/* 標題更像介紹型網站 */
body.cnmc-sky h1{ font-size: 42px !important; margin: 18px 0 6px !important; letter-spacing:.5px; }
body.cnmc-sky h2{ font-size: 26px !important; margin: 34px 0 12px !important; padding-bottom:10px; border-bottom:1px solid rgba(15,23,42,.10); }
body.cnmc-sky p{ color:#475569 !important; }

/* 圖片卡片化：你站上很多裸圖，這個最有感 */
body.cnmc-sky img{
  border-radius: 16px !important;
  box-shadow: 0 12px 30px rgba(2,6,23,.18) !important;
  display:block;
  margin: 14px auto;
  max-width:100%;
  height:auto;
}

/* 你若有 <hr> 就變好看；沒有也不會亂 */
body.cnmc-sky hr{
  border:0 !important;
  height:1px !important;
  background: linear-gradient(90deg, transparent, rgba(37,99,235,.35), transparent) !important;
  margin: 18px 0 !important;
}

/* 導覽列如果本來就有樣式，這只加「質感」不推翻 */
body.cnmc-sky nav,
body.cnmc-sky header{
  backdrop-filter: blur(10px);
  background: rgba(255,255,255,.65) !important;
  border-bottom: 1px solid rgba(15,23,42,.10) !important;
}
